<template>
  <v-card>
    <v-card-title>
      <mrc_ess_switch true-value="0"
                      v-if="$store.state.uuid"
                      false-value="1"
                      :readonly="disabled"
                      v-model="list.status"
                      :true-text="$t('label_normal')"
                      :false-text="$t('label_time_out')"></mrc_ess_switch>
    </v-card-title>
    <v-card-text>
      <v-row>
        <v-col cols="6">
          <!-- <v-text-field label="任务名称*"
                              :readonly="disabled"
                              :error-messages="$errors('list','job_name',requiredRule)"
                              v-model="list.job_name"
                              >
                </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_The_name_of_the_task')+'*'"
                             :isNational="judegNational('job_name')"
                             :readonly="disabled"
                             :error-messages="$errors('list','job_name',requiredRule)"
                             v-model="list.job_name">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="6">
          <mrc_ess_prompt :label="$t('label_grouping')+'*'"
                          :disabled='disabled'
                          :rules='false'
                          :chips="false"
                          :errMsg="$errors('list','job_group',requiredRule)"
                          v-model="list.job_group"
                          obj="group_name">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="12">
          <!-- <v-text-field label="调用目标字符串*"
                              :error-messages="$errors('list','invoke_target',requiredRule)"
                              :readonly="disabled"
                              v-model="list.invoke_target">
                </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Call_target_string')+'*'"
                             :isNational="judegNational('invoke_target')"
                             :error-messages="$errors('list','invoke_target',requiredRule)"
                             :readonly="disabled"
                             v-model="list.invoke_target">
          </mrc_ess_multiText>
          <div class="d-flex flex-column-reverse">
            <span class="d-flex">
              <span>
                <v-icon class="pr-2 material-icons #FAFAFA--text">
                  priority_high
                </v-icon>
              </span>
              <span>
                {{$t('label_Bean_invocation_example')}} :ryTask.ryParama('ry')
              </span>
            </span>
            <span>
              <span>
                <v-icon class="pr-2 material-icons">
                  priority_high
                </v-icon>
              </span>
              <span>
                {{$t('label_Class_Class_invocation_example')}}:com.ruoyi.quarts.task.ryTask.ryParamas('ry')
              </span>
            </span>
            <span>
              <span>
                <v-icon class="pr-2 material-icons">
                  priority_high
                </v-icon>
              </span>
              <span>
                {{$t('label_Parameters_that')}}:{{$t('label_String_support')}}、{{$t('label_Boolean_type')}}、{{$t('label_Long_integer_type')}}、{{$t('label_Swim_point_type')}}、{{$t('label_The_integer')}}
              </span>
            </span>
          </div>
        </v-col>
        <v-col cols="12">
          <!-- 0/30 * * * * ? * -->
          <!-- <v-text-field v-model="list.cron_expression"
                              :readonly="disabled"
                              :error-messages="$errors('list','cron_expression',requiredRule)"
                              label="Cron 表达式*">
                </v-text-field> -->
          <mrc_ess_multiText v-model="list.cron_expression"
                             :readonly="disabled"
                             :isNational="judegNational('cron_expression')"
                             :error-messages="$errors('list','cron_expression',requiredRule)"
                             :label="$t('label_Cron_expression')+'*'">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <v-radio-group v-model="list.misfire_policy"
                         row>
            <v-radio :label="$t('label_Executed_immediately')"
                     value="1"></v-radio>
            <v-radio :label="$t('label_Perform_a')"
                     value="2"></v-radio>
            <v-radio :label="$t('label_Give_up_to_perform')"
                     value="3"></v-radio>
          </v-radio-group>
        </v-col>
        <v-col cols="12">
          <v-checkbox v-model="list.concurrent"
                      true-value="1"
                      false-value="0"
                      :label="list.concurrent?$t('label_Allow_concurrent_execution'):$t('label_disabledConcurrentExecution')"></v-checkbox>
        </v-col>
        <v-col cols="12">
          <!-- <v-text-field label="备注"
                              :readonly="disabled"
                              v-model="list.remark">
                </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_note')"
                             :isNational="judegNational('remark')"
                             :readonly="disabled"
                             v-model="list.remark">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       @success='success'></mrc_ess_submitBtn>
  </v-card>
</template>
<style scoped>
</style> 
<script>  
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { verifyCron, deatailTimedTask, addTimedTask, updateTimedTask, deleteTimedTask, updateTimedTaskStatus, getLogList, executeTimedTask } from '@/service/api/timedTask.js'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      items: [],
      list: {
        misfire_policy: '1',
        concurrent: '1'
      },
    };
  },
  validations: {
    list: {
      job_name: {
        required
      },
      job_group: {
        required
      },
      invoke_target: {
        required
      },
      cron_expression: {
        required
      }
    }
  },
  created () { },
  async mounted () {
    //mrbase_sys_timed_task    定时任务表             mrbase_sys_timed_task_log 定时任务日志表
    await this.national('mrbase_sys_timed_task')
    if (this.$store.state.uuid) {
      this.getDetail()
      // this.$route.meta.title = this.$store.state.subPage_name
      this.$route.meta.title = this.$store.getters.subPage_name
    }
    else {

    }
  },
  methods: {
    async getDetail () {
      const res = await deatailTimedTask({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
      else {

      }
    },
    async verifyCron () {
      const res = await verifyCron({ cron_expression: this.list.cron_expression })
      if (res.code == 200) {
        return true
      }
      this.$store.commit('newsTips', 'Cron' + this.$t('message_Expression_error'))
      return false
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑嘛?'
      }).then(res => {
        this.$router.push({
          name: 'timedTask'
        })
      }).catch(err => { })
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      const res = await this.verifyCron()
      if (!res) return
      if (this.$store.state.uuid) {
        updateTimedTask(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        this.list.status = '1'
        addTimedTask(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'timedTask' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }

    }
  },
  computed: {

  },
  watch: {},
};
</script>

<style scoped>
</style>
