//定时任务
import http from '../httpConfig'

//验证cron语句是否正确
export const verifyCron = (params = {}) => {
  return new Promise((resolve, reject) => {
    http.post('/api/job/checkCronExpressionIsValid/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

